'use client'

import Image from 'next/image'
import { useTranslation } from 'next-i18next'
import { places } from '../data' // Import your places data

type Props = {
  params: {
    id: string
  }
}

export default function PlacePage({ params }: Props) {
  const placeId = params.id
  const place = places.find(p => p.name.toLowerCase() === placeId.toLowerCase())

  if (!place) {
    return (
      <p className='p-10 text-center text-red-500 text-lg'>
        Place not found.
      </p>
    )
  }

  const { t } = useTranslation()

  return (
    <div className='min-h-screen bg-white p-6 sm:p-12'>
      <div className='max-w-5xl mx-auto grid md:grid-cols-2 gap-10'>
        <div className='rounded-xl overflow-hidden shadow-lg border border-gray-200'>
          <Image
            src={place.image}
            alt={place.name}
            width={600}
            height={700}
            className='w-full h-[600px] object-cover'
            priority
          />
        </div>
        <div className='flex flex-col justify-center space-y-6'>
          <h1 className='text-3xl font-bold text-gray-800'>{place.name}</h1>
          <p className='text-gray-600'>{place.description}</p>
          
          <div className='space-y-2'>
            <p className='text-lg font-medium'>{place.travelTime}</p>
            <p className='text-lg font-medium'>{place.price}</p>
          </div>

          <div className='flex items-center gap-4 pt-4'>
            <button className='bg-indigo-600 hover:bg-indigo-700 text-white px-6 py-2 text-base rounded-md'>
              {t('bookNow')}
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}